import {
  IViewDesignerComponentModel,
  RX_VIEW_DEFINITION,
  RxViewComponentRegistryService
} from '@helix/platform/view/api';
import { ViewDesignerComponentModel } from '@helix/platform/view/designer';
import { filter, map } from 'rxjs/operators';

export class PageDesignModel extends ViewDesignerComponentModel implements IViewDesignerComponentModel {
  private rxViewComponentRegistryService: RxViewComponentRegistryService = this.injector.get<
    RxViewComponentRegistryService
  >(RxViewComponentRegistryService);

  componentName$ = this.sandbox.getChildComponents().pipe(
    filter((childComponents) => childComponents.length > 0),
    map((childComponents) => {
      const pageComponentType = childComponents[0].type;
      const descriptor = this.rxViewComponentRegistryService.get(pageComponentType);

      return descriptor?.name || RX_VIEW_DEFINITION.unknownPageComponent.name;
    })
  );
}
